import React, { useState, forwardRef, useEffect } from 'react';
import { Table, Input, InputNumber, Popconfirm, Form, Button } from 'antd';
import { Icon, } from "@/components";

interface Item {
  key: string;
  [propName: string]: any;
}

let tableWidth = 150
interface EditableCellProps extends React.HTMLAttributes<HTMLElement> {
  editing: boolean;
  dataIndex: string;
  title: any;
  inputType: 'number' | 'string';
  record: Item;
  index: number;
  children: React.ReactNode;
}

const EditableCell: React.FC<EditableCellProps> = ({
  editing,
  dataIndex,
  title,
  inputType,
  record,
  index,
  children,
  ...restProps
}) => {
  const inputNode = inputType === 'number' ? <InputNumber /> : <Input />;
  return (
    <td {...restProps}>
      {editing ? (
        <Form.Item
          name={dataIndex}
          style={{ margin: 0 }}
          rules={[
            {
              required: true,
              message: `请输入 ${title}!`,
            },
          ]}
        >
          {inputNode}
        </Form.Item>
      ) : (
        children
      )}
    </td>
  );
};

const EditableTable = forwardRef((props, ref) => {
  const { onChange, columns = [], value = [], readOnly, title } = props
  const [form] = Form.useForm();
  const [data, setData] = useState(value);
  const [editingKey, setEditingKey] = useState('');
  let formInit = {}
  columns.forEach(item => {
    formInit[item.dataIndex] = item.inputType === 'number' ? '' : 0
  })

  useEffect(() => {
    if (value.length) {
      setData(value)
    }
  }, [value])

  const isEditing = (record: Item) => record.key === editingKey;

  const edit = (record: Partial<Item> & { key: React.Key }) => {
    form.setFieldsValue({ ...formInit, ...record });
    setEditingKey(record.key);
  };

  const cancel = () => {
    setEditingKey('');
  };

  const save = async (key: React.Key) => {
    try {
      const row = (await form.validateFields()) as Item;
      const newData = [...data];
      const index = newData.findIndex(item => key === item.key);
      if (index > -1) {
        const item = newData[index];
        newData.splice(index, 1, {
          ...item,
          ...row,
        });
        setData(newData);
        setEditingKey('');
      } else {
        newData.push(row);
        setData(newData);
        setEditingKey('');
      }
      if (onChange) onChange(newData)
    } catch (errInfo) {
      console.log('Validate Failed:', errInfo);
    }
  };

  const add = () => {
    const newData = [...data];
    let key = newData.length + 1
    setEditingKey(key);
    newData.push({ key, ...formInit, })
    setData(newData)
  }

  const remove = (key: React.Key) => {
    const newData = [...data];
    const index = newData.findIndex(item => key === item.key);
    newData.splice(index, 1);
    setData(newData)
  }

  if (Array.isArray(columns)) {
    tableWidth = 150
    columns.forEach(item => {
      item.editable = true
      tableWidth += 150
    })
  }
  const _columns = [
    ...(columns || []),
  ];
  if (!readOnly) {
    _columns.push({
      title: '操作',
      width: 150,
      fixed: 'right',
      render: (_: any, record: Item) => {
        const editable = isEditing(record);
        return editable ? (
          <span>
            <Button type="link" size="small" onClick={() => save(record.key)} style={{ marginRight: 8 }}>
              保存
            </Button>
            <Popconfirm title="确定取消吗?" onConfirm={cancel}>
              <Button type="link" size="small">取消</Button>
            </Popconfirm>
          </span>
        ) : (
          <span>
            <Button type="link" size="small" disabled={editingKey !== ''} onClick={() => edit(record)}>
              编辑
            </Button>
            <Popconfirm title="确定删除吗?" onConfirm={() => remove(record.key)}>
              <Button type="link" size="small" disabled={editingKey !== ''}>
                删除
              </Button>
            </Popconfirm>
          </span>
        );
      },
    })
  }

  const mergedColumns = _columns.map(col => {
    if (!col.editable) {
      return col;
    }
    return {
      ...col,
      onCell: (record: Item) => ({
        record,
        inputType: col.inputType,
        dataIndex: col.dataIndex,
        title: col.title,
        editing: isEditing(record),
      }),
    };
  });

  return (
    <Form form={form} component={false}>
      <Table
        components={{
          body: {
            cell: EditableCell,
          },
        }}
        title={() =>
          !readOnly ?
            <span>
              {title} :
              <Button
                type="text"
                icon={<Icon iconName="Add" marginRight={5} color="#007eff" />}
                style={{ margin: 5 }}
                onClick={add}
              >添加记录</Button>
            </span>
            : null
        }
        bordered
        dataSource={data}
        columns={mergedColumns}
        rowClassName="editable-row"
        size="small"
        pagination={{
          onChange: cancel,
        }}
        scroll={{ x: tableWidth }}
      />
    </Form>
  );
});

export default EditableTable